// 
const state = {
    // 
    userInfo: null,
    // token
    token: null
}

// 
const initState = () => {
    const token = uni.getStorageSync('token')
    const userInfo = uni.getStorageSync('userInfo')
    //  token  userInfo 
    if (token && userInfo) {
        state.token = token
        state.userInfo = userInfo
    } else {
        state.token = null
        state.userInfo = null
        // 
        uni.removeStorageSync('token')
        uni.removeStorageSync('userInfo')
    }
}

// 
initState()

// token
export const getToken = () => {
    //  Storage 
    if (!state.token) {
        const token = uni.getStorageSync('token')
        const userInfo = uni.getStorageSync('userInfo')
        //  token  userInfo 
        if (token && userInfo) {
            state.token = token
            state.userInfo = userInfo
        } else {
            // 
            state.token = null
            state.userInfo = null
            uni.removeStorageSync('token')
            uni.removeStorageSync('userInfo')
        }
    }
    return state.token
}

// 
export const getUserInfo = () => {
    //  Storage 
    if (!state.userInfo) {
        const token = uni.getStorageSync('token')
        const userInfo = uni.getStorageSync('userInfo')
        //  token  userInfo 
        if (token && userInfo) {
            state.token = token
            state.userInfo = userInfo
        } else {
            // 
            state.token = null
            state.userInfo = null
            uni.removeStorageSync('token')
            uni.removeStorageSync('userInfo')
        }
    }
    return state.userInfo
}

// token
export const setToken = (token) => {
    //  token  null userInfo
    if (!token) {
        clearUserInfo()
        return
    }
    state.token = token
    uni.setStorageSync('token', token)
}

// 
export const setUserInfo = (userInfo) => {
    //  userInfo  null token
    if (!userInfo) {
        clearUserInfo()
        return
    }
    state.userInfo = userInfo
    uni.setStorageSync('userInfo', userInfo)
}

// 
export const clearUserInfo = () => {
    state.userInfo = null
    state.token = null
    uni.removeStorageSync('token')
    uni.removeStorageSync('userInfo')
}

// 
export const checkLogin = () => {
    const token = getToken()
    const userInfo = getUserInfo()
    return !!(token && userInfo)
}

// 
export const autoLogin = async () => {
    const token = getToken()
    if (!token) return false

    try {
        // token
        const userInfo = await uni.$u.http.get('/hotel-member/member/getInfo')
        if (userInfo) {
            setUserInfo(userInfo)
            return true
        }
        console.warn('')
        return false
    } catch (error) {
        console.error(':', error)
        return false
    }
}

export default state