// 
import {gaodeKey} from "../common/api";

let locationPromise = null;

export const getLocation = () => {
    // Promise
    if (locationPromise) {
        return locationPromise;
    }

    // 
    locationPromise = new Promise((resolve, reject) => {
        // 
        uni.getSetting({
            success: (res) => {
                if (!res.authSetting['scope.userLocation']) {
                    // 
                    uni.authorize({
                        scope: 'scope.userLocation',
                        success: () => {
                            doGetLocation(resolve, reject);
                        },
                        fail: (err) => {
                            console.error('', err);
                            uni.showModal({
                                title: '',
                                content: '',
                                success: (modalRes) => {
                                    if (modalRes.confirm) {
                                        uni.openSetting();
                                    }
                                    reject(err);
                                }
                            });
                        }
                    });
                } else {
                    doGetLocation(resolve, reject);
                }
            },
            fail: (err) => {
                console.error('', err);
                reject(err);
            }
        });
    });

    // Promise
    locationPromise.finally(() => {
        locationPromise = null;
    });

    return locationPromise;
};

// 
const doGetLocation = (resolve, reject) => {
    uni.getLocation({
        type: 'gcj02',
        isHighAccuracy: true,
        success: async (res) => {
            const key =await gaodeKey()
            // 
            uni.request({
                url: 'https://restapi.amap.com/v3/geocode/regeo',
                data: {
                    key: key,
                    location: `${res.longitude},${res.latitude}`,
                    extensions: 'all',
                    output: 'json'
                },
                success: (result) => {
                    if (result.data.status === '1') {
                        const addressComponent = result.data.regeocode.addressComponent;

                        // 
                        let city = addressComponent.city || addressComponent.province || '';
                        if (Array.isArray(city)) city = city[0];
                        city = city || '';
                        if (typeof city === 'string' && (city.endsWith('') || city.endsWith(''))) {
                            // city 
                        } else if (addressComponent.province) {
                            city = addressComponent.province;
                        }
                        // 
                        let displayCity = city;
                        // 
                        let district = addressComponent.district || '';
                        if (Array.isArray(district)) district = district[0];
                        district = district || '';

                        resolve({
                            city: displayCity || '',
                            district: district,
                            address: result.data.regeocode.formatted_address,
                            longitude: res.longitude,
                            latitude: res.latitude,
                            pois: result.data.regeocode.pois
                        });
                    } else {
                        reject(new Error(''));
                    }
                },
                fail: (err) => {
                    console.error('', err);
                    reject(err);
                }
            });
        },
        fail: (err) => {
            console.error('', err);
            reject(err);
        }
    });
}; 