import config from './config';

/**
 * 
 *  http.post/http.get
 */
class Upload {
  constructor() {
    this.baseUrl = config.baseUrl || '';
  }

  /**
   * 
   * @param {String} filePath - 
   * @param {Object} options - 
   * @param {String} options.url -  /hotel-file/file/upload
   * @param {String} options.name -  key file
   * @param {Object} options.formData - 
   * @param {Function} options.onProgress - 
   * @returns {Promise}
   */
  uploadFile(filePath, options = {}) {
    const {
      url = '/hotel-file/upload',
      name = 'file',
      formData = {},
      onProgress
    } = options;

    return new Promise((resolve, reject) => {
      const uploadUrl = this.baseUrl + url;
      const token = uni.getStorageSync('token');

      console.log(':', {
        uploadUrl,
        filePath,
        hasToken: !!token
      });

      const uploadTask = uni.uploadFile({
        url: uploadUrl,
        filePath: filePath,
        name: name,
        formData: formData,
        header: {
          'Authorization': token
        },
        success: (res) => {
          console.log(':', res);
          try {
            //  HTTP 
            if (res.statusCode !== 200) {
              console.error(' - HTTP:', res.statusCode);
              reject(new Error(`: ${res.statusCode}`));
              return;
            }

            const data = JSON.parse(res.data);
            console.log(':', data);
            
            if (data.code === '200') {
              //  data  name  url
              resolve(data.data);
            } else {
              console.error(' - :', data);
              reject(new Error(data.msg || ''));
            }
          } catch (error) {
            console.error(':', error, res.data);
            reject(new Error(''));
          }
        },
        fail: (err) => {
          console.error(':', err);
          reject(new Error(err.errMsg || ''));
        }
      });

      // 
      if (onProgress && typeof onProgress === 'function') {
        uploadTask.onProgressUpdate((res) => {
          onProgress(res.progress);
        });
      }
    });
  }

  /**
   * 
   * @param {Array} filePaths - 
   * @param {Object} options - 
   * @returns {Promise<Array>}  URL 
   */
  uploadFiles(filePaths, options = {}) {
    const uploadPromises = filePaths.map(filePath => this.uploadFile(filePath, options));
    return Promise.all(uploadPromises);
  }

  /**
   * 
   * @param {Object} options - 
   * @param {Number} options.count -  9
   * @param {Array} options.sizeType -  ['compressed']
   * @param {Array} options.sourceType -  ['album', 'camera']
   * @param {String} options.url - 
   * @param {Function} options.onProgress - 
   * @returns {Promise<Array>}  URL 
   */
  chooseAndUploadImage(options = {}) {
    const {
      count = 9,
      sizeType = ['compressed'],
      sourceType = ['album', 'camera'],
      ...uploadOptions
    } = options;

    return new Promise((resolve, reject) => {
      uni.chooseImage({
        count,
        sizeType,
        sourceType,
        success: async (res) => {
          try {
            const urls = await this.uploadFiles(res.tempFilePaths, uploadOptions);
            resolve(urls);
          } catch (error) {
            reject(error);
          }
        },
        fail: (err) => {
          reject(err);
        }
      });
    });
  }

  /**
   * 
   * @param {Object} options - 
   * @param {Array} options.sourceType -  ['album', 'camera']
   * @param {Number} options.maxDuration -  60 
   * @param {String} options.url - 
   * @returns {Promise<String>}  URL
   */
  chooseAndUploadVideo(options = {}) {
    const {
      sourceType = ['album', 'camera'],
      maxDuration = 60,
      ...uploadOptions
    } = options;

    return new Promise((resolve, reject) => {
      uni.chooseVideo({
        sourceType,
        maxDuration,
        success: async (res) => {
          try {
            const url = await this.uploadFile(res.tempFilePath, uploadOptions);
            resolve(url);
          } catch (error) {
            reject(error);
          }
        },
        fail: (err) => {
          reject(err);
        }
      });
    });
  }
}

// 
const upload = new Upload();

export default upload;
